import React from 'react';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import s from './Profile.module.css';

const Profile = (props) => {
  return (
    <div className={s.profile}>
      <ProfileInfo profile={props.profile} status={props.status} updateStatus={props.updateStatus} isOwner={!props.match.params.userId} savePhoto={props.savePhoto} saveProfile={props.saveProfile}/>
      <MyPostsContainer/>
    </div>
  );
};

export default Profile;