import React from 'react';
import { Input, createField, Textarea } from '../../common/FormsControls/FormsControls';
import { reduxForm } from "redux-form";
import s from './ProfileInfo.module.css';
import style from '../../common/FormsControls/FormsControls.module.css';
import cn from 'classnames';

const ProfileDataForm = ({ handleSubmit, profile, error }) => {
  return (
    <form onSubmit={handleSubmit} className={s.profileForm} >
      <div className={s.mainInfo}>
        <div>
          <button className={s.saveBtn}>Save</button>
        </div>
        {error && <div className={style.formSummaryError}>{error}</div>}
        <div className={s.profileDataFormItem}>
          <span>Full name: </span> {createField('Full name', 'fullName', [], Input)}
        </div>
        <div className={s.profileDataFormItem}>
          <span>Looking for a job: </span> {createField('', 'lookingForAJob', [], Input, 'checkbox')}
        </div>
        <div className={s.profileDataFormItem}>
          <span>My professional skills: </span>
          {createField('My professional skills', 'lookingForAJobDescription', [], Textarea)}
        </div>
        <div className={s.profileDataFormItem}>
          <span>About me: </span>
          {createField('About me', 'aboutMe', [], Textarea)}
        </div>
      </div>
      <div className={s.contacts}>
        <span>Contacts: </span> {Object.keys(profile.contacts).map(key => {
          return <div key={key} className={cn(s.contact, s.profileDataFormItem)} >
            <span>{key}:</span> {createField(key, 'contacts.' + key, [], Input)}
          </div>
        })}
      </div>
    </form>
  )
}

const ProfileDataFormReduxForm = reduxForm({ form: 'edit-profile' })(ProfileDataForm);
export default ProfileDataFormReduxForm;

