import React, { useState } from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import userPhoto from './../../../assets/images/user.png';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';
import ProfileDataFormReduxForm from './ProfileDataForm';

const ProfileInfo = ({ profile, status, updateStatus, isOwner, savePhoto, saveProfile }) => {

  const [editeMode, setEditMode] = useState(false);

  if (!profile) {
    return <Preloader />
  }

  const onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
      savePhoto(e.target.files[0]);
    }
  }

  const onSubmit = (formData) => {
    saveProfile(formData).then(() => {
      setEditMode(false);
    });
  }

  return (
    <div className={s.descriptionBlock}>
      <div className={s.user}>
        <div className={s.userPhoto}>
          <img src={profile.photos.large || userPhoto} alt='' />
          {isOwner && <label>
            Refresh photo
          <input type='file' onChange={onMainPhotoSelected} />
          </label>}
        </div>
        <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />
      </div>
      <div className={s.profileInner}>
        {editeMode
          ? <ProfileDataFormReduxForm profile={profile} initialValues={profile} onSubmit={onSubmit} />
          : <ProfileData goToEditMode={() => { setEditMode(true) }} profile={profile} isOwner={isOwner} />}
      </div>
    </div>
  );
}

const ProfileData = ({ profile, isOwner, goToEditMode }) => {
  return (
    <div className={s.profileData}>
      {isOwner && <div><button className={s.editBtn} onClick={goToEditMode} >Edit</button></div>}
      <div className={s.name}>
        {profile.fullName}
      </div>
      <div className={s.profileDataItem}>
        <span>Looking for a job: </span> {profile.lookingForAJob ? 'yes' : 'no'}
      </div>
      {profile.lookingForAJob &&
        <div className={s.profileDataItem}>
          <span>My professional skills: </span> {profile.lookingForAJobDescription}
        </div>
      }
      <div className={s.profileDataItem}>
        <span>About me: </span> {profile.aboutMe}
      </div>
      <div className={s.profileDataItem}>
        <span>Contacts: </span> {Object.keys(profile.contacts).map(key => {
          return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]} />
        })}
      </div>
    </div>
  )
}

const Contact = ({ contactTitle, contactValue }) => {
  return <div className={s.contact}><span>{contactTitle}</span>: {contactValue}</div>
}

export default ProfileInfo;
